___CSS_LOADER_EXPORT___.push([module.id, `* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: Arial, sans-serif;
}

body {
  background-color: #fff;
  color: #333;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-start;
  min-height: 100vh;
}

app-bar {
  width: 100%;
  background-color: #0057a6;
  color: white;
  padding: 20px 0;
  text-align: center;
}

app-bar h1 {
  font-size: 2.5rem;
  font-weight: bold;
}

/* Form styling */
note-form {
  display: block;
  max-width: 700px;
  margin: 50px auto;
  padding: 0 20px;
}

note-form input[type="text"],
note-form textarea {
  width: 100%;
  padding: 15px;
  margin-bottom: 15px;
  border: 1px solid #0057a6;
  border-radius: 5px;
  font-size: 1rem;
  color: #999;
  text-align: center;
}

note-form textarea {
  height: 120px;
  resize: none;
}

note-form input[type="text"]::placeholder,
note-form textarea::placeholder {
  color: #ccc;
  text-align: center;
}

note-form button {
  width: 100%;
  padding: 15px;
  background-color: #0057a6;
  color: white;
  border: none;
  border-radius: 5px;
  font-size: 1.2rem;
  cursor: pointer;
}

#notes-list {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(300px, 1fr));
  gap: 20px;
  max-width: 700px;
  margin: 0 auto 50px;
  padding: 0 20px;
}

note-item {
  display: block;
  border: 1px solid #0057a6;
  border-radius: 5px;
  padding: 20px;
  text-align: center;
}

note-item h3 {
  font-size: 2rem;
  font-weight: bold;
  margin-bottom: 5px;
}

note-item p {
  font-size: 1.2rem;
  margin-bottom: 15px;
}

note-item small {
  color: #666;
  font-size: 0.9rem;
}`, "",{"version":3,"sources":["webpack://./styles.css"],"names":[],"mappings":"AAAA;EACE,SAAS;EACT,UAAU;EACV,sBAAsB;EACtB,8BAA8B;AAChC;;AAEA;EACE,sBAAsB;EACtB,WAAW;EACX,aAAa;EACb,sBAAsB;EACtB,mBAAmB;EACnB,2BAA2B;EAC3B,iBAAiB;AACnB;;AAEA;EACE,WAAW;EACX,yBAAyB;EACzB,YAAY;EACZ,eAAe;EACf,kBAAkB;AACpB;;AAEA;EACE,iBAAiB;EACjB,iBAAiB;AACnB;;AAEA,iBAAiB;AACjB;EACE,cAAc;EACd,gBAAgB;EAChB,iBAAiB;EACjB,eAAe;AACjB;;AAEA;;EAEE,WAAW;EACX,aAAa;EACb,mBAAmB;EACnB,yBAAyB;EACzB,kBAAkB;EAClB,eAAe;EACf,WAAW;EACX,kBAAkB;AACpB;;AAEA;EACE,aAAa;EACb,YAAY;AACd;;AAEA;;EAEE,WAAW;EACX,kBAAkB;AACpB;;AAEA;EACE,WAAW;EACX,aAAa;EACb,yBAAyB;EACzB,YAAY;EACZ,YAAY;EACZ,kBAAkB;EAClB,iBAAiB;EACjB,eAAe;AACjB;;AAEA;EACE,aAAa;EACb,4DAA4D;EAC5D,SAAS;EACT,gBAAgB;EAChB,mBAAmB;EACnB,eAAe;AACjB;;AAEA;EACE,cAAc;EACd,yBAAyB;EACzB,kBAAkB;EAClB,aAAa;EACb,kBAAkB;AACpB;;AAEA;EACE,eAAe;EACf,iBAAiB;EACjB,kBAAkB;AACpB;;AAEA;EACE,iBAAiB;EACjB,mBAAmB;AACrB;;AAEA;EACE,WAAW;EACX,iBAAiB;AACnB","sourcesContent":["* {\r\n  margin: 0;\r\n  padding: 0;\r\n  box-sizing: border-box;\r\n  font-family: Arial, sans-serif;\r\n}\r\n\r\nbody {\r\n  background-color: #fff;\r\n  color: #333;\r\n  display: flex;\r\n  flex-direction: column;\r\n  align-items: center;\r\n  justify-content: flex-start;\r\n  min-height: 100vh;\r\n}\r\n\r\napp-bar {\r\n  width: 100%;\r\n  background-color: #0057a6;\r\n  color: white;\r\n  padding: 20px 0;\r\n  text-align: center;\r\n}\r\n\r\napp-bar h1 {\r\n  font-size: 2.5rem;\r\n  font-weight: bold;\r\n}\r\n\r\n/* Form styling */\r\nnote-form {\r\n  display: block;\r\n  max-width: 700px;\r\n  margin: 50px auto;\r\n  padding: 0 20px;\r\n}\r\n\r\nnote-form input[type=\"text\"],\r\nnote-form textarea {\r\n  width: 100%;\r\n  padding: 15px;\r\n  margin-bottom: 15px;\r\n  border: 1px solid #0057a6;\r\n  border-radius: 5px;\r\n  font-size: 1rem;\r\n  color: #999;\r\n  text-align: center;\r\n}\r\n\r\nnote-form textarea {\r\n  height: 120px;\r\n  resize: none;\r\n}\r\n\r\nnote-form input[type=\"text\"]::placeholder,\r